<template>

</template>

<script>
// 
const typeArr = ["primary", "success", "warning", "info", "danger"];
export default {
  name: "vuxBadge",
  props: {
    value: [String, Number],
    type: {
      type: String,
      validator(val) {
        // console.log("", val);
        // propsvue
        if (typeArr.includes(val)) {
          return true;
        } else {
          return false;
        }
      },
    },
    max: {
      type: Number,
      validator(val) { // 
        if (val) {
          if (typeof val === "number") {
            return true;
          } else {
            return false;
          }
        } else {
          return true;
        }
      },
    },
    isDot: Boolean,
    isShow: {
      // 
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      typeArr,
    };
  },
  computed: {
    showValue() {
      // 
      if (this.isDot) {
        return;
      }
      // 
      if (this.max) {
        return this.value > this.max ? `${this.max}+` : this.value;
      }
      // 
      else {
        return this.value;
      }
    },
    showSup() {
      // isShowfalse
      if (!this.isShow) {
        return false;
      }
      // 
      if (this.value === "") {
        return false;
      }
      // 
      return true;
    },
  },
};
</script>

