    right: 3px; // transform
  }

  sup {
    color: #fff;
    background-color: #f56c6c; // 
    border-radius: 10px; // 
    padding: 1px 4px;
    font-size: 12px; // 
    transform: translate(100%, -50%); // 
    white-space: nowrap; // css
  }

  // 
  .primary {
    background-color: #409eff;
  }

  .success {
    background-color: #67c23a;
  }

  .warning {
    background-color: #e6a23c;
  }

  .info {
    background-color: #909399;
  }

  .danger {
